
// 

import { useParams, useNavigate } from "react-router-dom"
import axios from "axios"
import { useEffect, useState, useMemo, useCallback } from "react"
import { useAxios, forceLogin } from "../components/hooks"
import _ from 'lodash'
import './ViewVote.css'
import dayjs from "dayjs"
import { useImmer } from "use-immer"
import { message, Spin, Button, Modal, List, Tag } from "antd"
import { LoadingOutlined, ArrowLeftOutlined, SelectOutlined, SmileOutlined, ProfileOutlined } from '@ant-design/icons'

/**
 * 
 * 1. 
 * 2. comet
 * hold
 * 
 * 3.websocket
 *
 */

function ViewVote({ userInfo: userData }) { //userInfo
    const navigator = useNavigate()
    // 
    const userId = userData.result.userId //ID
    const { voteId } = useParams()
    // const { data: userData, loading: userLoading, error: userError } = useUser() //
    const { data, loading, error } = useAxios({ //useAxiosws
        url: 'https://vote.nekoda.cn/vote/' + voteId,
        method: 'GET'
    }, voteId)
    const [userVotes, setUserVotes] = useState() //ws

    const [selectedOptionIds, setSelectedOptionIds] = useImmer([]) // 

    //ws
    useEffect(() => {
        // console.log('useEffect ws');
        //
        if (loading || Date.now() > new Date(data.result.deadline).valueOf()) { //
            return
        }
        // ws://localhost:8081/realtime-voteinfo/voteId
        const wsURL = `${window.location.protocol.replace('http', 'ws')}//${window.location.host}/realtime-voteinfo/${voteId}`
        console.log(wsURL);
        const ws = new WebSocket(wsURL)
        ws.addEventListener('message', e => {
            let data = JSON.parse(e.data) //  userVotes <json>
            console.log('ws', data);
            setUserVotes(v => data)
        })
        return () => {
            console.log('ws');
            ws.close()
        }
    }, [voteId, loading])


    // {voteId,userId,title,..,options[{optionId,content,voteId}],userVotes[{optionId,avatar,userId,name}]}
    let vote = data?.result ?? {} // 
    // console.log('vote', vote);
    let thisPageUserVotes = (userVotes ?? vote.userVotes) || [] //vote.userVotes [{optionId, avatar, userId, name}]
    // console.log('thisPageUserVotes', thisPageUserVotes);
    let groupedVotes = useMemo(() => _.groupBy(thisPageUserVotes, 'optionId'), [thisPageUserVotes])//optionId
    let uniqueUsers = useMemo(() => _.uniqBy(thisPageUserVotes, 'userId'), [thisPageUserVotes]) //
    let totalUsers = uniqueUsers.length; //
    console.log('<ViewVote>groupedVotes', groupedVotes);
    // console.log('totalUsers:', totalUsers);

    //
    const hasExpired = useMemo(() => {
        return dayjs(vote.deadline).isBefore(Date.now())
    }, [voteId, loading])

    // ||   ==>  || 
    const handleOptionClick = (option) => {
        // debugger
        if (vote.anonymous) {
            //
            if (thisPageUserVotes.some(opration => opration.userId === userId) || hasExpired) {
                //
                return
            }
            selectOption(option, vote.multiple) //
        } else {
            voteOption(option) //
        }
    }

    //   /. ws
    async function voteOption(option) {
        // debugger;
        if (hasExpired) { // 
            message.error('')
            return
        }
        if (!vote.anonymous) { //
            const { optionId } = option
            try {
                await axios.post(`/vote/${voteId}`, {
                    optionIds: [optionId]
                })
            } catch (e) { // ||  ||  ||  || 
                message.error('')
                // console.log(e.toJSON()); //
            }
        } else { //
            try {
                await axios.post(`https://vote.nekoda.cn/vote/${voteId}`, {
                    optionIds: selectedOptionIds
                })
            } catch (e) {
                message.error('')
            }
            setSelectedOptionIds(draft => [])
        }
    }

    //  || 
    function selectOption(option, multiple) {
        // debugger;
        const optionId = option.optionId
        let idx = selectedOptionIds.indexOf(optionId)
        if (multiple) { //
            if (idx === -1) {
                setSelectedOptionIds(draft => {
                    draft.push(optionId)
                })
            } else {
                setSelectedOptionIds(draft => {
                    draft.splice(idx, 1)
                })
            }
        } else { //
            console.log('::', optionId);
            setSelectedOptionIds(draft => {
                return [optionId]
            })
            console.log('selectedOptionIds:::', selectedOptionIds);
        }
    }

    //
    const [isModalVisible, setIsModalVisible] = useState(false)
    const showHelp = useCallback(() => {
        setIsModalVisible(true)
    }, [])
    const closeHelp = useCallback(() => {
        setIsModalVisible(false)
    }, [])
    const helpData = useMemo(() => ([{
        title: '/',
        description: ''
    }, {
        title: '/',
        description: ''
    }, {
        title: '',
        description: <>{'     '}
            <Button type="primary" style={{ borderRadius: '50%' }} title="" icon={<SelectOutlined />} />
            {'     '}</>
    }]), [])

    // <-
    const goMyVotes = useCallback(() => {
        navigator('/myVotes')
    }, [])
    const goBack = useCallback(() => {
        navigator(-1)
    }, [])

    //
    const getTagColor = (type) => {
        if (type === 'multiple') {
            if (vote.multiple) {
                return 'geekblue'
            } else {
                return 'blue'
            }
        } else if (type === 'anonymous') {
            if (vote.anonymous) {
                return 'red'
            } else {
                return 'orange'
            }
        } else if (type === 'deadline') { //
            return '#F7711C'
        }
        return 'black'
    }

    //
    const shareLink = useCallback(() => {
        const el = document.createElement('textarea')
        el.value = 'http://vote.nekoda.cn/view-vote/' + voteId
        el.setAttribute('readonly', '')
        el.style.position = 'absolute'
        el.style.left = '-9999px'
        document.body.append(el)
        el.select()
        document.execCommand('copy')
        document.body.removeChild(el)
        message.success('')
    }, [voteId])

    // /
    const [showAvatar, setShowAvatar] = useState(true)
    const toggleAvatar = useCallback(() => {
        setShowAvatar(s => !s)
    }, [])

    //
    if (loading) {
        return (<div className="viewVoteSpin">
            <Spin indicator={<LoadingOutlined />} />
        </div>)
    }
    if (error instanceof Error) {
        console.log('<viewvote>axios', error);
        message.error(',')
        // throw error // 
    }


    return (
        <div className="viewVotes">
            <header>
                <h1></h1>
                <div>
                    <Button onClick={showHelp}></Button>
                    <Button onClick={goMyVotes}></Button>
                    <Button type="primary" onClick={goBack}><ArrowLeftOutlined /></Button>
                </div>
            </header>
            {/*  */}
            <main>
                <List header={
                    <header className="listheader">
                        <section>
                            <h2 title={vote.description}>{vote.title}</h2>
                            <div>[]:  {vote.description}</div>
                            <div className="voteTag">
                                <Tag color={getTagColor("multiple")}>{vote.multiple ? '' : ''}</Tag>
                                <Tag color={getTagColor("anonymous")}>{vote.anonymous ? '' : ''}</Tag>
                                {hasExpired ? <Tag color={getTagColor("deadline")}>{''}</Tag> : null}
                            </div>
                        </section>
                        <aside>
                            <Button type={showAvatar ? "primary" : ""} icon={showAvatar ? <SmileOutlined /> : <ProfileOutlined />} onClick={toggleAvatar} />
                            <Button type="primary" onClick={shareLink} title="" icon={<SelectOutlined />} />
                        </aside>
                    </header>}
                    dataSource={vote.options} bordered itemLayout="vertical"
                    renderItem={(option) => { //option: {voteId, optionId, content}
                        let thisOptionVoted = groupedVotes[option.optionId] || [] // {optionId,name,avatar,userId}
                        return (<List.Item key={option.optionId} onClick={() => handleOptionClick(option)} className="listItem">
                            <div>
                                <article>[{option.content}]</article>
                                <aside>
                                    {/*  */}
                                    {(thisOptionVoted.some(user => user.userId === userId) ||
                                        selectedOptionIds.includes(option.optionId))
                                        ? '[]'
                                        : null
                                    }
                                    <span>[{thisOptionVoted.length}]</span>
                                    <span>[{totalUsers == 0 ? 0 : (thisOptionVoted.length * 100 / totalUsers).toFixed(2)}%]</span>
                                </aside>
                            </div>
                            {thisOptionVoted.length == 0 ? null : <div className="votedUsers">
                                {
                                    thisOptionVoted.map((oneVote, idx) => (
                                        showAvatar ? <img key={idx} style={{ width: 32, height: 32 }} className='user-avatar' title={oneVote.name || ''}
                                            src={oneVote.avatar ? `https://vote.nekoda.cn${oneVote.avatar}` : 'http://vote.nekoda.cn/uploads/default.jpg'} />
                                            : <span> {oneVote.name ?? ''} </span>
                                    ))
                                }
                            </div>}
                        </List.Item>)
                    }}
                />
            </main>
            <footer>
                <p>{dayjs(vote.deadline).format('YYYY-MM-DD HH:mm')}</p>
                {/*  */}
                {!!vote.anonymous && !hasExpired &&
                    !thisPageUserVotes.some(opration => opration.userId == userId)
                    && <Button type="primary" disabled={!selectedOptionIds.length} onClick={voteOption}></Button>
                }
            </footer>

            {/*  */}
            <Modal title="" visible={isModalVisible} onOk={closeHelp} onCancel={closeHelp} centered={true}>
                <List
                    itemLayout="vertical"
                    dataSource={helpData}
                    renderItem={(item, idx) => (
                        <List.Item key={idx}>
                            <List.Item.Meta title={item.title} description={item.description} />
                        </List.Item>
                    )}
                />
            </Modal>
        </div>
    )
}







export default forceLogin(ViewVote)