
// import axios from "../api" //API1
import axios from "axios"
import { useEffect, useCallback, useRef, useContext } from "react"
import { Link, useNavigate } from "react-router-dom"
import { useInput, useUser } from '../components/hooks'
import { Button, Input, message } from "antd"
import { EyeInvisibleOutlined, EyeTwoTone, UserOutlined, ArrowLeftOutlined } from "@ant-design/icons"
import './Login.css'
import { CurMenu } from "../App"

// 
export default function Login() {
    const userEmail = useInput()
    const password = useInput()
    const navigate = useNavigate()
    const userInfo = useUser() // /account/current-user -> useAxios userInfo=={data, loading, error, update}
    const loginData = useRef({})
    loginData.current = {
        email: userEmail.value,
        password: password.value
    }
    const menuInfo = useContext(CurMenu)
    //
    useEffect(() => {
        if (!userInfo.data || userInfo.error || userInfo.loading || userInfo.data.code === -1) {

        } else if (userInfo.data.code === 0) {
            navigate(-1)
        }
    }, [])

    //  axios
    const userLogin = useCallback(async function userLogin() {
        const info = loginData.current //Capture Value
        //axios,data
        // let response = await axios.post('/account/login', info, {
        //     withCredentials: true //Cookie fetch"credentials:'include'"
        // }) //API

        try {
            let response = await axios.post('https://vote.nekoda.cn:443/account/login', info) //APIcookie
            // let data = response.data //
            console.log('', response);
            //cookie
            userInfo.update() //cookie<RequireLogin>
            navigate(-1)
        } catch (e) { //1. >=400 axios 2.
            // console.log(e.toJSON());
            // console.log(e.response.data); //
            if (e.response) {
                message.error('')
                console.log('Login', e.response.data) //  {code : -1, msg: 'xxx'} 
            } else {
                // alert('you have net error, then axios failed request.')
                message.error('..')
            }
        }
    }, [])

    const backHome = useCallback(() => {
        navigate('/')
        menuInfo.updateCurMenuData(['1'])
    }, [])

    return (
        <div className="login">
            <header>
                <h2>nekoda</h2>
                <Button type="primary" onClick={backHome}><ArrowLeftOutlined /></Button>
            </header>
            <main>
                <div>
                    <Input placeholder="" {...userEmail} suffix={<UserOutlined />}
                        addonBefore="" />
                </div>
                <div>
                    <Input.Password placeholder="6" {...password}
                        addonBefore=""
                        iconRender={visible => (visible ? <EyeTwoTone /> : <EyeInvisibleOutlined />)} />
                </div>
                <div>
                    <span><Button type="primary" onClick={userLogin}></Button></span>
                    <span><Link to="/forget-password">?</Link></span>
                </div>
            </main>
        </div>
    )
}