import { useCallback, useContext, useEffect, useMemo, useState } from "react"
import axios from "axios"
import { UserContext } from "../views/CurrentUserInfo"
import RequireLogin from "../views/RequireLogin"

export function useInput(initVal = '', predicate = null) {
    const [val, setVal] = useState(initVal)

    const onChange = useCallback(function onChange(e) {
        setVal(val => e.target.value)
    }, [predicate])

    return useMemo(() => ({
        value: val,
        onChange
    }), [val, predicate])
}


export function useBooleanInput(initVal = false, predicate = null) {
    const [checked, setChecked] = useState(initVal)

    const onChange = useCallback(function (e) {
        setChecked(checked => !checked)
    }, [predicate])

    return useMemo(() => ({
        checked,
        onChange
    }), [checked, predicate])
}


export function useAxios(config, listenChanger = '') {
    const [data, setData] = useState() //
    const [loading, setLoading] = useState(true) //
    const [error, setError] = useState() //
    const [shouldUpdate, setUpdate] = useState(0) //

    // useEffect dom
    // useLayoutEffectcomponentDidMount dom
    useEffect(() => {
        const controller = new AbortController()
        const req = axios({
            ...config,
            signal: controller.signal
        }).then(res => {
            console.log('useAxios', res);
            if (res.data.code === -1) {
                throw new Error('code -1, axios faild')
            }
            setData(d => res.data) // {code, result}
            setError(null)
            setLoading(l => false)
        }).catch(err => {
            setError(e => err)
            setLoading(l => false)
        })
        return () => {
            controller.abort()
        }
    }, [config.url, listenChanger, shouldUpdate])

    const update = useCallback(() => {
        //
        setLoading(l => true)
        setUpdate(c => c + 1) //Effect
    }, [])

    return { data, loading, error, update }
}


// hook,contextuseAxios
export function useUser() {
    const user = useContext(UserContext)
    return user // || 4xx user.error
}




//  
export function forceLogin(Comp) {
    function ForceLoginComp(props) {
        const userInfo = useUser()
        console.log('forceLogin', userInfo);

        if (userInfo.error) {
            //
            return <RequireLogin />
        }
        if (userInfo.loading) {
            return null
        }
        return <Comp {...props} userInfo={userInfo.data} update={userInfo.update} />
    }
    ForceLoginComp.displayName = 'ForceLogin ' + (Comp.displayName ?? Comp.name)
    return ForceLoginComp
}

